.NavigationItems{
    margin: 1rem;
    padding: 1rem;
    list-style: none;
    display: flex;
    align-items: center;
    height: 100%;
    width: 100%;
    justify-content: space-around;
    animation: moveToRight .7s ease-in;
}

@keyframes moveToRight {
    0%{
        opacity: 0;
        transform: translateX(-100px);
    }

    50%{
        opacity: 0.5;
        transform: translateX(20px);
    }

    100%{
        opacity: 1;
        transform: translate(0);
    }
}