.Main{
    margin:1px 0 0 0;
    padding: auto;
    background-image: url('../../../images/main-background.png');
    background-size: cover;
    height:89.8vh;
    display: flex;
    justify-content: space-between;
    background-position: center;
    
}

.LeftSide{
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 72vh;

    @media(max-width: 1200px){
        .LeftSide{
            padding: auto;
            margin:auto;
        }
    }
    
}


.WhatsappLogo{
    margin:0;
    padding: 1rem;
    img{
        height: 2rem;
        width: 2rem;

        &:hover{
            cursor: pointer;
        }
    }
}

.Texts{
    display: flex;
    flex-direction: column;
    
    h2{
        color: white;
        padding: 0 1rem ;
        margin:0 3rem 0 0;
        
    }
    h3{
        color:white;
        padding: 0 1rem;
        margin: 0 1rem 0 0;
        text-decoration: underline;
        text-transform: uppercase;
    }
    p{
        color: white;
        padding: 1rem ;
        text-align: justify;
        margin: 6px 3rem 0 0;
        letter-spacing: 1px;
        white-space: 1px;

    }
    @media (max-width:1300px){
        p{
            margin:0 10rem 0 0;
        }
        h3{
            margin:0 3rem 0 0;
        }
    }
   
}

.Icons{
    margin: 11rem 0 0 0;
    display: flex;
    height: 60vh;
    width: 20vh;
    
    p{  
       writing-mode: vertical-rl;
       text-orientation: mixed;
       margin: 2rem 0 0 0;
       padding: 1rem 0;
       color:white;
       font-size: 12px;
       font-weight: bold;

      
    }

    img{
        margin: 3.5rem 0 0 0;
        padding:1rem 0 0 0;
        height: 22vh;
        
    }
    @media (max-width:1200px){
        p{
            margin: 0;
            font-size:12px;
        }
    }
    
}

.RightSide{
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 78vh;
    

    ul{
        list-style: none;
        display:flex;
        margin:0 22rem;
       
        img{
            &:hover{
                cursor: pointer;
            }
        }
    }
    
    .Aimage{
        display: flex;
        margin:1rem 2rem 0 4rem;
        padding: 1rem;

        
    }

    .RightText{
        display: flex;
        flex-direction: column;
        width: 80%;
        margin:0 3rem;
        p{
            text-align: justify;
            word-spacing: 3px;
            letter-spacing: 1px;
        }

        img{
            height: 12vh;
            width: 40vh;
            margin:0;
        }

        a{
            text-decoration: underline;
            color:black;
        }

        a:hover{
            color:gray;
        }

        @media(max-width:1200px){
            p{
                margin:0 0 0 2rem;
            }

            img{
                margin: 1rem 0 0 2rem;
            }

        }


    }

    .ContactUs{
        display: flex;
        flex-direction: column;
         
        a{
            color:black;
            margin: 8rem 0 1rem 20rem;
            
        }
        form{
            margin:10px 0 0 8rem;
            input{
                margin: 2px;
                border-radius: 20px;
                border:1px solid lightgray;
                
                &::placeholder{
                    padding: 0.5rem;
                }
                
                &:focus{
                    outline: none;
                }
            }
            button{
                background-color:#61bafa;
                border-radius:20px;
                border: none;
                padding:3px 4px;
                color:black;   
                margin: 0 10px;
                width:20%;
                font-weight:400;
                text-transform: uppercase;

                &:hover{
                    background-color:black;
                    color:white;
                    cursor: pointer;
                }
            }
        }

        @media(max-width:1200px){
            margin:0 0 2rem 0;
        }
        
    }

}

