.NavigationItem{
    box-sizing: border-box;
    display: block;
    height: 100%;
    align-items: center;
    

    a{
        color:black;
        text-decoration: underline;
        box-sizing: border-box;
        text-transform: uppercase;

        &:hover{
            color:gray;
        }
        &:active{
            color:darkgray;
            text-decoration: none;
        }
        
    }
}

